import React from "react";

const Logos = () => {
  return (
    <div className="logos">
      <ul>
        <li>
          <a
            href="https://vvce.ac.in"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              src={
                "https://vvce.ac.in/wp-content/uploads/2020/03/vvce-logo.png"
              }
              alt="vvceLogo"
            />
          </a>
        </li>
        <li>
          <a
            href="https://vvce.acm.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              src={
                "https://upload.wikimedia.org/wikipedia/commons/thumb/8/8e/Association_for_Computing_Machinery_%28ACM%29_logo.svg/1200px-Association_for_Computing_Machinery_%28ACM%29_logo.svg.png"
              }
              alt="acmLogo"
            />
          </a>
        </li>
      </ul>
    </div>
  );
};

export default Logos;
